import {Input,Button,Grid,Row,Col}  from 'react-bootstrap';
import React from 'react'
import SearchLocationAction from '../actions/actions.es6'

import {LocationsStore} from '../stores/locationsStore.es6';

let value="";

let LocationsSearch = React.createClass({ 

 
  getInitialState() {
    return {
      fuzzy:false,
      country:false,
      records: [],
      value
    }
    this.onStoreChange = this.onStoreChange.bind(this);
  },

  componentDidMount() {
    LocationsStore.listen(this.onStoreChange);
  },

  componentWillUnmount() {
    LocationsStore.unlisten(this.onStoreChange);
  },

  onStoreChange(){
    console.log('Store changed ..');
  },

  onStatusChange(status) {
    this.setState({currentStatus: status});

  },

  handleChange(e) {
    debugger;

    let fuzzy=(e.target.name=='fuzzy')?!this.state.fuzzy:this.state.fuzzy;
      console.log('Fuzzy '+fuzzy);
      

    let country=(e.target.name=='country')?!this.state.country:this.state.country;
      console.log('Country '+country);
      
    let text=this.refs.text.getValue();

    let newState=Object.assign(this.state, {
        text,
        fuzzy,
        country
      }
  );

    this.setState(newState);
    
  },

  validationState() {
    let length = this.state.value.length;
    if (length > 3) return 'success';
    else if (length > 0) return 'error';
  },

  render() {
    console.log('render')
    debugger;
    return (
    <div className="container-fuild small" style ={{'width':'800px'}}>
      <form role="form" className="form-horizontal">
        <div className="form-group">
          <div className="col-sm-4">
            <Input 
                
                  type="text" 
                  value={this.state.text} 
                  placeholder="Enter text to search" 
                  bsStyle={this.validationState()} 
                  hasFeedback bsSize="small" 
                  ref="text" 
                  groupClassName="group-class" 
                  labelClassName="label-class" 
                  onChange={this.handleChange}/>
        </div>
        <div className="col-md-1">
          <input type="checkbox"  name="fuzzy"     checked={this.state.fuzzy} onChange={this.handleChange}/> Fuzzy 
        </div>
     
        <div className="col-md-1">
          <input type="checkbox"  name="country"   checked={this.state.country} onChange={this.handleChange}/> Country
        </div>

        <div className="col-md-1"><Button bsStyle="success" bsSize="small">Search</Button></div>
     </div>

  </form>
</div>)
}
})
export {LocationsSearch}


